"use client";

import Link from "next/link";
import { useEffect, useState } from "react";
import { format } from "date-fns";
import { ThemeToggle } from "@/components/ThemeToggle";
import { BatteryChip } from "@/components/BatteryChip";
import { useAppStore } from "@/lib/store";
import { useTodayStats } from "@/lib/hooks/useTodayStats";

export function NavBar() {
  const battery = useAppStore((state) => state.battery);
  const [now, setNow] = useState(new Date());
  const { stats } = useTodayStats();

  useEffect(() => {
    const id = setInterval(() => setNow(new Date()), 60_000);
    return () => clearInterval(id);
  }, []);

  const dateCopy = format(now, "EEE, MMM d");
  const timeCopy = format(now, "h:mm a");

  return (
    <header className="sticky top-0 z-40 w-full border-b bg-bg/95 backdrop-blur supports-[backdrop-filter]:bg-bg/60">
      <div className="container flex h-16 items-center justify-between px-4">
        <Link href="/" className="flex flex-col">
          <span className="text-sm text-muted-foreground">{dateCopy}</span>
          <span className="text-lg font-bold leading-none">Patna EV Co-Pilot</span>
        </Link>
        <div className="flex items-center gap-4">
          <div className="text-right text-xs">
            <p className="font-semibold text-foreground">â‚¹{stats.totalEarnings.toFixed(0)}</p>
            <p className="text-muted-foreground">Today | {stats.totalTrips} trips | {timeCopy}</p>
          </div>
          <BatteryChip percentage={battery} />
          <ThemeToggle />
        </div>
      </div>
    </header>
  );
}
