const isServer = () => typeof window === "undefined";

function readEnv(key: string) {
  try {
    return process.env[key];
  } catch {
    return undefined;
  }
}

export function getSupabaseConfig() {
  const url = isServer()
    ? readEnv("SUPABASE_URL") ?? readEnv("NEXT_PUBLIC_SUPABASE_URL")
    : readEnv("NEXT_PUBLIC_SUPABASE_URL");
  const anonKey = isServer()
    ? readEnv("SUPABASE_ANON_KEY") ?? readEnv("NEXT_PUBLIC_SUPABASE_ANON_KEY")
    : readEnv("NEXT_PUBLIC_SUPABASE_ANON_KEY");

  if (!url) {
    throw new Error("Missing SUPABASE_URL/NEXT_PUBLIC_SUPABASE_URL environment variable");
  }
  if (!anonKey) {
    throw new Error("Missing SUPABASE_ANON_KEY/NEXT_PUBLIC_SUPABASE_ANON_KEY environment variable");
  }

  return { url, anonKey };
}
